// Library Imports
import React, { Component } from "react";
import { connect } from "react-redux";

// Relative Imports
import { Content } from "../styles";
import { Added, Add } from "./styles";
import Header from "../../UI/Header/Centered";
import Aux from "../../../Aux";
import List from "../../UI/List/Detail";
import ListContainer from "../../UI/List/Container";
import P from "../../../_Text/P";
import Button from "../../../_buttons/button";

class SettingsDetail extends Component {
  render() {
    let nationalCurrencies = this.props.accounts.filter(
      row =>
        row.symbol !== "G" &&
        row.symbol !== "S" &&
        row.symbol !== "₿" &&
        row.symbol !== "H"
    );

    let nationalCurrencyWallets = nationalCurrencies.map(account => {
      return (
        <List
          symbol={account.symbol}
          name={account.token}
          subname={account.ticker + " Wallet"}
          info={!account.visible ? <Add /> : <Added />}
          onClick={() => this.props.visibleaccounts(account.ticker)}
        />
      );
    });

    let digitalCurrencies = this.props.accounts.filter(
      row => row.symbol === "₿" || row.symbol === "H"
    );
    let digitalCurrencyWallets = digitalCurrencies.map(account => {
      return (
        <List
          key={account.ticker}
          symbol={account.symbol}
          name={account.token}
          subname={account.ticker + " Wallet"}
          info={!account.visible ? <Add /> : <Added />}
          onClick={() => this.props.visibleaccounts(account.ticker)}
        />
      );
    });
    return (
      <Aux>
        <Header
          H1={"Currency Wallets"}
          P="Click to add or remove wallets:"
          close={this.props.close}
          back={() => this.props.routing("Overview", "")}
        />
        <Content>
          <P styling={"margin: 0px 0px 10px 0px"}>National Currencies:</P>
          <ListContainer>{nationalCurrencyWallets}</ListContainer>
          <P styling={"margin: 40px 0px 10px 0px"}>Digital Currencies:</P>
          <ListContainer>{digitalCurrencyWallets}</ListContainer>
        </Content>
      </Aux>
    );
  }
}

const mapStateToProps = state => ({
  nativeCurrency: state.NativeCurrency,
  accounts: state.Accounts
});

export const mapDispatchToProps = dispatch => ({
  visibleaccounts: ticker =>
    dispatch({
      type: "ACCOUNTS_CHANGE_VISIBILITY",
      ticker: ticker
    })
});

export default connect(mapStateToProps, mapDispatchToProps)(SettingsDetail);
