// Library Imports
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  getNativeCurrencyPrice,
  changeNativeCurrency
} from "../../../../actions";

// Relative Imports
import { Content } from "../styles";
import { Check, Unchecked } from "./styles";
import Header from "../../UI/Header/Centered";
import Aux from "../../../Aux";
import List from "../../UI/List/Detail";
import ListContainer from "../../UI/List/Container";
import P from "../../../_Text/P";

class SettingsDetail extends Component {
  NativeCurrency = () => {
    return this.props.nativeCurrency.options.map(currency => {
      let isNativeCurrency =
        this.props.nativeCurrency.ticker === currency.ticker;
      return (
        <List
          symbol={currency.symbol}
          name={currency.name}
          subname={currency.ticker}
          info={isNativeCurrency ? <Check /> : <Unchecked />}
          onClick={() => this.updateNativeCurrency(currency.ticker)}
          styling={
            isNativeCurrency
              ? "width: 100%; border: none; background-color: rgba(0,0,0,0.2); border-radius: 10px; padding: 20px;"
              : "padding: 20px; border: none; &:hover{width: 100%; border: none; background-color: rgba(0,0,0,0.2); border-radius: 10px; padding: 20px;}"
          }
        />
      );
    });
  };

  updateNativeCurrency = ticker => {
    this.props.changeNativeCurrency(ticker);
    this.props.getNativeCurrencyPrice(ticker);
  };

  render() {
    return (
      <Aux>
        <Header
          H1={"Native Currency"}
          P="Pick a native currency:"
          close={this.props.close}
          back={() => this.props.routing("Overview", "")}
        />
        <Content>
          <ListContainer
            styling={"background-color: transparent; padding: 0px;"}
          >
            {this.NativeCurrency()}
          </ListContainer>
        </Content>
      </Aux>
    );
  }
}

const mapStateToProps = state => ({
  nativeCurrency: state.NativeCurrency
});

export default connect(mapStateToProps, {
  changeNativeCurrency,
  getNativeCurrencyPrice
})(SettingsDetail);
