// Library Imports
import React, { Component } from "react";
import { connect } from "react-redux";
import { decrypt } from "../../../../../utility/utility-encrypt";

// Relative Imports
import { Container, Password } from "./styles";
import { Content } from "../styles";
import Header from "../../UI/Header/Centered";
import Aux from "../../../Aux";
import P from "../../../_Text/P";

import Button from "../../../_buttons/button";

class SettingsDetail extends Component {
  state = {
    psk: "",
    seed: "",
    show: false
  };

  componentDidMount() {
    Promise.all([
      decrypt(this.props.keys.mnemonic_string),
      decrypt(this.props.keys.sec_spendKey_string)
    ]).then(data => {
      this.setState({ seed: data[0], psk: data[1] });
    });
  }

  passwordVisibility(oldState) {
    this.setState({ show: !oldState });
  }
  render() {
    const {
      pub_spendKey_string,
      sec_viewKey_string,
      pub_viewKey_string
    } = this.props.keys;
    const { psk, seed } = this.state;

    const keys = {
      pub_spendKey_string,
      sec_viewKey_string,
      pub_viewKey_string,
      psk,
      seed
    };

    return (
      <Aux>
        <Header
          H1={"Passwords"}
          P="View wallet passwords:"
          close={this.props.close}
          back={() => this.props.routing("Overview", "")}
        />
        <Content>
          <P styling={"margin: 0px 0px 10px 0px"}>Wallet Password:</P>
          <Container>
            <Password
              type="text"
              value={keys.seed}
              styling={this.state.show ? "" : "filter: blur(5px)"}
            />
            <Button
              label={this.state.show ? "HIDE PASSWORD" : "SHOW PASSWORD"}
              styling={
                " border-top: 1px solid #363638; border-radius: 0px; width: 100%;  background-color: transparent; &:hover { background-color: transparent;} "
              }
              onClick={() => this.passwordVisibility(this.state.show)}
            />
          </Container>
        </Content>
      </Aux>
    );
  }
}

const mapStateToProps = state => ({
  keys: state.keys
});

export default connect(mapStateToProps, {})(SettingsDetail);
