import React, { Component } from "react";
import { connect } from "react-redux";

import Aux from "../../../../Aux";
import Header from "../../../UI/_Header/Default";
import Content from "../../../UI/Content";
import P from "../../../../_Text/P";
import Button from "../../../../_buttons/button";
import Shortcut from "../../../../Shortcut";
import { selectSimplePrice } from "shared/reducers/simplePrice";
import { readableAmounts } from "utility/Utilites";
import ListContainer from "../../../UI/List/Container";
import List from "../../../UI/List/";
import Tab from "../../../../tab";
import BitcoinLogo from "../../../../../../assets/icons/bitcoin.svg";
import EthereumLogo from "../../../../../../assets/icons/ethereum.png";
import MoneroLogo from "../../../../../../assets/icons/monero.svg";
import DepositIcon from "../../../../../../assets/icons/deposit.png";
import ExchangeIcon from "../../../../../../assets/icons/exchanged.png";

class BuyOptions extends Component {
  state = {
    page: "Cryptocurrency",
    paymentcurrencies: [
      {
        name: "Bitcoin",
        icon: BitcoinLogo,
        symbol: "₿",
        ticker: "BTC",
        price: 7800
      },
      {
        name: "Ethereum",
        icon: EthereumLogo,
        symbol: "Ξ",
        ticker: "ETH",
        price: 140
      },
      {
        name: "Monero",
        icon: MoneroLogo,
        symbol: "M",
        ticker: "XMR",
        price: 56
      }
    ]
  };

  routing = page => {
    this.setState({ page: page });
  };

  Options = () => {
    switch (this.state.page) {
      case "Wallet Funds":
        return (
          <ListContainer>
            <List
              symbol={"$"}
              type={"Symbol"}
              name={"xUSD Wallet"}
              subname={"420.32 xUSD"}
              info={
                this.props.data.symbol +
                " " +
                readableAmounts((1 / this.props.lastPrice).toFixed(2))
              }
              reference={" /xUSD"}
              onClick={() =>
                this.props.paymentmethod(
                  ["xUSD", "US Dollar", "$", , 0, "Amount"],
                  "PaymentMethod"
                )
              }
            />
          </ListContainer>
        );
      case "Cryptocurrency":
        return (
          <ListContainer>
            {this.state.paymentcurrencies.map(paymentcurrency => {
              return (
                <List
                  key={paymentcurrency.symbol}
                  icon={paymentcurrency.icon}
                  name={paymentcurrency.name}
                  subname={paymentcurrency.ticker}
                  info={
                    this.props.data.symbol +
                    " " +
                    readableAmounts(
                      (paymentcurrency.price / this.props.lastPrice).toFixed(2)
                    ) +
                    " "
                  }
                  reference={"/" + paymentcurrency.ticker}
                  onClick={() =>
                    this.props.paymentmethod(
                      [
                        paymentcurrency.ticker,
                        paymentcurrency.name,
                        paymentcurrency.symbol,
                        paymentcurrency.icon,
                        this.props.buy.amount / paymentcurrency.price,
                        "Deposit"
                      ],
                      "PaymentMethod"
                    )
                  }
                />
              );
            })}
          </ListContainer>
        );
    }
  };

  render() {
    return (
      <Aux>
        <Header
          S={this.props.data.symbol}
          H4={"Get " + this.props.data.ticker}
          P={"Get " + this.props.data.token + " with:"}
          close={() => this.props.linking(this.props.data.ticker, "Default")}
          content={
            <Tab
              firstTabLabel="CRYPTOCURRENCY"
              secondTabLabel="WALLET FUNDS"
              firstTabState={this.state.page === "Cryptocurrency"}
              secondTabState={this.state.page === "Wallet Funds"}
              firstTabClickEvent={() => this.routing("Cryptocurrency")}
              secondTabClickEvent={() => this.routing("Wallet Funds")}
            />
          }
        />
        <Content styling={"padding: 130px 20px 20px 20px;"}>
          {this.Options()}
        </Content>
      </Aux>
    );
  }
}

const mapStateToProps = state => ({
  nativeCurrency: state.NativeCurrency,
  lastPrice: selectSimplePrice(state)
});

export default connect(mapStateToProps, {})(BuyOptions);
