import React, { Component } from "react";
import * as clipboard from "clipboard-polyfill";

import Aux from "../../../../Aux";
import Header from "../../../UI/_Header/Default";
import Content from "../../../UI/Content";
import Button from "../../../../_buttons/button";
import TextInput from "../../../../_Input/Text";
import Tab from "../../../../tab";

class SendAddress extends Component {
  state = {
    page: "Haven",
    placeholder: "Haven wallet address",
    address: ""
  };

  ref = React.createRef();
  componentDidMount() {
    this.refs.SendAddressInput.focus();
  }

  routing = page => {
    this.setState({ page: page });
  };

  keyPress = event => {
    if (event.key === "Enter" && this.props.send.address.length > 97) {
      this.ref.current.onClick();
    }
  };

  paste = () => {
    clipboard.readText().then(response => {
      this.setState({
        address: response
      });
      if (this.state.address.length > 97) {
        this.props.address(this.state.address, "AddressPaste");
      }
    });
  };

  tab = tab => {
    this.setState({ page: tab });
    switch (tab) {
      case "Haven":
        this.setState({ placeholder: "Haven wallet address" });
        break;
      case "Cryptocurrency":
        this.setState({
          placeholder: "Bitcoin or Ethereum wallet address"
        });
        break;
      case "Email":
        this.setState({ placeholder: "Email address" });
        break;
    }
    this.refs.SendAddressInput.focus();
  };

  render() {
    let isHavenAddress = this.props.send.address.length > 97;

    return (
      <Aux>
        <Header
          S={this.props.data.symbol}
          H4={"Send " + this.props.data.ticker}
          P={"Pick a destination:"}
          close={() => this.props.linking(this.props.data.ticker, "Default")}
          styling={"box-shadow: none;"}
          content={
            <Aux>
              <Tab
                firstTabLabel="HAVEN"
                secondTabLabel="CRYPTOCURRENCY"
                thirdTabLabel="EMAIL"
                firstTabState={this.state.page === "Haven"}
                secondTabState={this.state.page === "Cryptocurrency"}
                thirdTabState={this.state.page === "Email"}
                firstTabClickEvent={() => this.tab("Haven")}
                secondTabClickEvent={() => this.tab("Cryptocurrency")}
                thirdTabClickEvent={() => this.tab("Email")}
              />
            </Aux>
          }
        />
        <Content styling={" padding: 130px 20px 20px 20px"}>
          <TextInput
            placeholder={this.state.placeholder}
            value={this.props.send.address}
            onChange={event => this.props.address(event, "AddressInput")}
            onKeyPress={this.keyPress}
            styling={[
              isHavenAddress
                ? "width: calc(100% - 116px);"
                : "width: calc(100% - 122px); ",
              "border-radius: 10px 0px 0px 10px;"
            ].join("")}
            ref="SendAddressInput"
          />
          {isHavenAddress ? (
            <Button
              onClick={() => this.props.routing("Amount")}
              label="NEXT"
              highlight
              styling={"border-radius: 0px 10px 10px 0px; "}
              ref="SendAddressButton"
            />
          ) : (
            <Button
              onClick={() => this.paste()}
              label="PASTE"
              styling={
                " border-radius: 0px 10px 10px 0px; font-weight: 500; background-color: rgba(0,0,0,0.075);  &:hover {background-color: rgba(0,0,0,0.075)}"
              }
            />
          )}
        </Content>
      </Aux>
    );
  }
}
export default SendAddress;
