// Library Imports
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  getForex,
  getSimplePrice,
  getNativeCurrencyPrice
} from "../../actions";
import { readableAmounts } from "../../../utility/Utilites";

// Relative Imports

import { NO_PRICE } from "../../reducers/priceHistory";
import { calcValue, convertToMoney, convertToPrice } from "utility/utility";
import { selectWebSyncState } from "../../../platforms/web/reducers/chain";

import Aux from "../Aux";
import Header from "../Header";
import Content from "../Content";
import { Account } from "./styles";
import Loader from "./_Pages/Loader";
import Default from "./_Pages/Default/index";
import Send from "./_Pages/_Send/index";
import Receive from "./_Pages/Receive";
import Buy from "./_Pages/_Buy/index";
import Menu from "./_Pages/Menu";
import History from "./_Pages/_History";
import ComingSoon from "./_Pages/ComingSoon";
import MoreIcon from "../../../assets/icons/settings.svg";
import Button from "../../components/_buttons/button";
import { Spinner } from "../spinner";
import { selectSimplePrice } from "../../reducers/simplePrice";

class Wallet extends Component {
  state = {
    forexPriceFetched: false,
    mobile: false,
    action: "",
    ticker: ""
  };

  routing = (ticker, action) => {
    if (action !== "Default") {
      this.setState({ ticker: ticker });
      this.setState({ action: action });
    } else {
      this.setState({ ticker: "" });
      this.setState({ action: "" });
    }
  };

  componentDidMount() {
    window.scrollTo(0, 0);
    this.props.getNativeCurrencyPrice(this.props.nativeCurrency.ticker);
    if (this.props.price === NO_PRICE) {
      this.props.getSimplePrice();
      this.props.getForex();
    }
  }

  DesktopAccounts = () => {
    return this.props.accounts.map(data => {
      const { token, ticker, symbol, balance, available, visible } = data;

      const rates = this.props.rates;
      let price = rates[ticker] ? rates[ticker] : 0;
      price = symbol + price.toFixed(2);

      if (
        visible ||
        convertToMoney(this.props.balances[data.ticker].balance) > 0
      ) {
        return (
          <Account
            key={token}
            styling={
              this.state.ticker !== ticker ? "background-color: #363638;" : null
            }
          >
            {this.Page(ticker, data, available, price)}
          </Account>
        );
      }
    });
  };

  Page = (ticker, data, available, price) => {
    if (this.state.ticker !== ticker) {
      return (
        <Default
          data={data}
          routing={this.routing.bind(this)}
          nativeCurrency={this.props.nativeCurrency}
        />
      );
    }
    if (available) {
      switch (this.state.action) {
        case "Send":
          return (
            <Send
              data={data}
              routing={this.routing.bind(this)}
              balance={convertToMoney(this.props.balances[data.ticker].balance)}
            />
          );
        case "Receive":
          return <Receive data={data} routing={this.routing.bind(this)} />;
        case "Buy":
          return (
            <Buy
              data={data}
              routing={this.routing.bind(this)}
              price={price}
              nativeCurrency={this.props.nativeCurrency}
            />
          );
        case "History":
          return <History data={data} routing={this.routing.bind(this)} />;
      }
    } else {
      return <ComingSoon data={data} routing={this.routing.bind(this)} />;
    }
  };

  render() {
    let totalBalance = convertToPrice(
      convertToMoney(this.props.balances["XHV"].balance) * this.props.price
    ).toFixed(2);

    return (
      <Aux>
        <Header
          H1={
            this.props.syncState.isSyncing
              ? (
                  (this.props.syncState.scannedHeight /
                    this.props.syncState.blockHeight) *
                  100
                ).toFixed(2) + "%"
              : this.props.nativeCurrency.symbol +
                readableAmounts(
                  (totalBalance * this.props.nativeCurrency.price).toFixed(2)
                )
          }
          P={
            this.props.syncState.isSyncing
              ? "Syncing Wallet..."
              : "Total Balance in " + this.props.nativeCurrency.ticker
          }
          icon={MoreIcon}
          buttons={
            <Button
              icon
              iconSrc={MoreIcon}
              onClick={() => this.props.routing("Settings")}
              styling={"float:right;"}
            />
          }
        />
        <Content>
          {this.props.syncState.isSyncing ? (
            <Aux>
              <Loader /> <Loader />
            </Aux>
          ) : (
            this.DesktopAccounts()
          )}
        </Content>
      </Aux>
    );
  }
}

const mapStateToProps = state => ({
  ...state.simplePrice,
  ...state.forex,
  balances: state.xBalance,
  lastPrice: selectSimplePrice(state),
  nativeCurrency: state.NativeCurrency,
  accounts: state.Accounts,
  syncState: selectWebSyncState(state)
});

export default connect(mapStateToProps, {
  getSimplePrice,
  getNativeCurrencyPrice,
  getForex
})(Wallet);
